			</div>
		</div> -->
		<div class='circle-container'>
			<div class="circle-item">
				<div class="circle-left-wrap">
					<div class="left" :style="{'transform': 'rotate('+(jindu>=50?(360*((jindu-50)/100)):0)+'deg)'}">
					</div>
				</div>
				<div class="circle-right-wrap">
					<div class="right" :style="{'transform': 'rotate('+(jindu>=50?180:(360*(jindu/100)))+'deg)'}"></div>
				</div>
				<div class='mask'></div>
			</div>
		</div>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				jindu: 30
			};
		},
	}
</script>
<style lang="scss">
	.circle-container {
		position: relative;
		float: left;
		width: 84rpx;
		height: 84rpx;
		transform: scale(1.5);
		margin: 200rpx;
	}

	.circle-item {
		position: absolute;
		left: 0;
		top: 0px;
		width: 84rpx;
		height: 84rpx;
		border-radius: 50%;
		background-color: #9dc0ce;
	}

	.circle-left-wrap,
	.circle-right-wrap {
		position: absolute;
		left: 0;
		top: 0;
		width: 42rpx;
		height: 84rpx;
		overflow: hidden;
	}

	.circle-right-wrap {
		left: 42rpx;
	}

	.left,
	.right {
		position: absolute;
		top: 0;
		left: 0;
		width: 84rpx;
		height: 84rpx;
		border-radius: 50%;
		background: #e4e4e4;
	}

	.left {
		clip: rect(0, 42rpx, auto, 0);
	}

	.right {
		clip: rect(0, auto, auto, 42rpx);
		left: -42rpx;
	}

	.mask {
		position: absolute;
		top: 2rpx;
		left: 2rpx;
		width: 80rpx;
		height: 80rpx;
		border-radius: 50%;
		background-color: #fff;
	}
</style>

